/**
* 执法过程-视频库组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                  @row-click="seeFunction" @selection-change="handleSelectionChange" border size="mini">
            <!-- <el-table-column type="selection" width="55"></el-table-column> -->
            <el-table-column align="center" label="视频名称" prop="videoName" show-overflow-tooltip/>
            <el-table-column align="center" label="播放地址" prop="videoUrl" show-overflow-tooltip/>
            <el-table-column align="center" label="视频大小" prop="videoSize"/>
            <el-table-column align="center" label="视频时长" prop="videoTime"/>
            <el-table-column align="center" label="执法人员" prop="officials"/>
            <el-table-column align="center" label="操作" width="50">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-view btn-link"
                                 type="success"><i></i></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
            <!-- <el-table-column align="center" label="操作" width="150">
                <template slot-scope="scope">
                     <el-tooltip class="item" effect="dark" content="删除" placement="top-end">
                        <el-link type="danger" @click.stop="delateFunction(scope.row)" class="el-icon-delete" style="margin:20px"><i></i></el-link>
                     </el-tooltip>
                </template>
            </el-table-column> -->
        </el-table>

        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getAccountList" class="page-css"/>

        <!-- 查看 -->
        <seeContent :editId='seeObj.editId' :show.sync='seeObj.show' :title='seeObj.title'></seeContent>
    </div>
</template>

<script>
    import {apiCaseVideobatchDelete, apiCaseVideodDelete, apiCaseVideoSearch} from "../../../api/case";
    import LjPagination from '../../../components/LjPagination'
    import LjSearchFrom from '../../../components/LjSearchForm'
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import seeContent from "./seeContent";

    export default {
        name: "videoPage",
        data() {
            return {
                //查看数据
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
                input: '',
                list: [],
                rowdata: {},
                idarr: [],
                arrstr: ''
            }
        },
        components: {
            LjPagination, LjSearchFrom, seeContent
        },

        methods: {
            handleSelectionChange(valarr) {
                let arr = []
                valarr.forEach(item => {
                    arr.push(item.vid)
                })
                let arrstr = arr.join(',')
                this.arrstr = arrstr;
            },
            //查询+搜索
            getAccountList() {

                apiCaseVideoSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                })
            },
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = '视频详情查看';
                this.seeObj.editId = row.videoUrl;
            },

            //删除
            delateFunction(row) {
                this.$confirm('此操作将删除条数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseVideodDelete(row.vid).then(res => {
                        this.getAccountList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    });
                })
            },
            //批量删除
            batchDelete() {
                this.$confirm('此操作将删除多条数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseVideobatchDelete(this.arrstr).then(res => {
                        this.getAccountList();
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    });
                })
            },
            //搜索
            searchList(data) {  //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiCaseVideoSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                })

            },

        },
        filters: {
            ifsex(val) {
                return IFSEX[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            }

        },
        computed: {},
        mounted() {

        },

        created() {
            this.getAccountList();
        },
    }
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px
    }

    .btn-link {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
