<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" append-to-body
               top='5vh'
               width="80%">

        <div class="detail-block">

            <div class="enforceRight-block">
                <el-form :model="form" class="demo-ruleForm alert_wrop" label-width="100px" ref="ruleForm"
                         size="medium" status-icon>
                    <video :src="editId" controls height="600" width="100%">
                        <source type="video/mp4">
                    </video>

                </el-form>

            </div>

        </div>
    </el-dialog>

</template>

<script>
    import {formatDate, YESORNO} from "../../../until/wordBook";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            },

        },
        data() {
            return {
                form: {},
                enforceIdArr: [],//执法领域
                lawArr: [],  //法律法规
                fineArr: [],  //罚款

            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },

            //弹框弹出时获取data给form

        },
        filters: {
            YESORNO(val) {
                return YESORNO[val]
            },
            formatDate(val) {
                return formatDate(val, 'yyyy-MM-dd')
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {

                }
            }
        },


    }
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold
    }


    .detail-block {
        font-size: @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
